/* -------- */

h1.photo-video-top-heading {
  color: #fff;
  font-family: 'sequel_sans_semi_bold_disp' !important;
  font-size: 32px;
  font-style: normal;
  font-weight: 100;
  line-height: normal;
  letter-spacing: 0px;
}

.photo-video-middle-heading {
  margin-top: 16px;
  color: #fff;
  font-family: "sequel_sans_heavy_disp", sans-serif !important;
  font-size: 88px;
  font-style: normal;
  font-weight: 100;
  line-height: 72px;
  letter-spacing: 0px;
}

.photo-video-heading-content {
  margin-top: 32px;
  max-width: 578px;
  width: 100%;
  color: #fff;
  font-family: "sequel_sans_roman_head" !important;
  font-size: 18px;
  font-style: normal;
  font-weight: 100;
  line-height: 140%;
}

.photo-video-heading-button {
  margin-top: 24px;
  border-radius: 60px;
  border: 1px solid #fff;
  color: #fff;
  background-color: transparent;
  text-align: center;
  -webkit-text-stroke-width: 0.2907530963420868;
  -webkit-text-stroke-color: #fff;
  font-family: "sequel_sans";
  padding: 13px 0px;
  font-size: 18px;
  font-style: normal;
  font-weight: 100;
  line-height: normal;
  width: 170px;
}
@media screen and (max-width: 767px) {
  .photo-video-page-parent {
    width: 100%;
    padding: 0 15px;
}
.photo-video-header-wrapper {
  padding: 32px 33px 43px 18px;
  max-width: 100%;
  margin-top: 0;
  text-align: left;
}
.photo-video-middle-heading {
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0;
}
p.photo-video-middle-heading br {
  display: none;
}
.photo-video-bussiness-management-wrapper {
  padding-top: 15px;
}
.photo-video-heading-content {
  margin-top: 0;
}
.photo-video-header-wrapper::before {
  position: absolute;
  right: 0;
  bottom: 0;
  display: inline-block;
  opacity: 0;
}
.photo-video-heading-button {
  margin-top: 15px;
}
.photo-video-bussiness-management-heading {
  font-size: 28px;
}
.PhotoAndVideo{
  position: absolute;
  width: 430px;
  height: 583px;
  right: 11px;
  bottom: 0;
  height: auto;
  width: 132px;
}

}